.faqs-section {
  padding: 0 2rem;
  position: relative;
  overflow-x: clip;
}

.planet1 {
  z-index: -1;
  animation: 3.2s linear infinite floatx;
  position: absolute;
  top: 25%;
  left: 0%;
}

.planet1 img {
  width: 80px;
  height: 100px;
}

.planet2 {
  z-index: -1;
  animation: 3.2s linear infinite floatx;
  position: absolute;
  top: 45%;
  right: -1%;
}

.planet2 img {
  width: 60px;
  height: 100px;
}

.faqs-section .container {
  justify-content: center;
  margin-top: 2rem;
  display: flex;
}

.faqs-section .container .col {
  width: 45%;
  padding: 10px;
}

.faqs-section .container .col > .question-box:first-child {
  margin-top: 0;
}

.faqs-section .container .question-box {
  background: var(--transparent-gray);
  border-radius: .5rem;
  margin: 1.5rem 0;
  padding: 1rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
}

.faqs-section .container .question-box:hover {
  background: #b2b2b24d;
}

.faqs-section .container .question-box .question {
  color: var(--white);
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 46px;
  font-size: 1.2rem;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.faqs-section .container .question-box .question.active {
  color: var(--primary-blue);
}

.faqs-section .container .question-box .question.active:after {
  transform: rotate(180deg);
}

.faqs-section .container .question-box .question:after {
  content: "";
  color: #fff;
  background-image: url("down.7a5e885d.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 2rem;
  height: 2rem;
  transition: transform .5s ease-in-out;
  display: block;
}

.faqs-section .container .question-box .answer {
  text-align: justify;
  opacity: 0;
  max-height: 0;
  font-size: 1rem;
  transition: max-height .5s ease-in-out, margin-top .4s ease-in-out, opacity .5s ease-in-out;
  overflow: hidden;
}

.faqs-section .container .question-box .answer.active {
  opacity: 1;
  max-height: 300px;
  margin-top: .5rem;
  transition: max-height 1s ease-in-out, margin-top .4s ease-in-out, opacity .8s ease-in-out;
}

@media screen and (max-width: 800px) {
  .faqs-section .container {
    flex-direction: column;
    align-items: center;
    padding: 0 1rem;
  }

  .faqs-section .container .col {
    width: 100%;
    padding: 0;
  }
}

@media screen and (max-width: 600px) {
  .faqs-section .container .question-box .question {
    height: auto;
  }

  .planet1 img, .planet2 img {
    width: 40px;
    height: 60px;
  }
}

